'use client'

import { useState, useEffect, useRef } from 'react'

interface AutoCommand {
  id: string
  name: string
  command: string
  delay: number
  isActive: boolean
  createdAt: string
  lastUsed?: string
  useCount: number
}

interface Guild {
  id: string
  name: string
  icon: string | null
}

export default function AutoComandoPage() {
  const [commands, setCommands] = useState<AutoCommand[]>([])
  const [guilds, setGuilds] = useState<Guild[]>([])
  const [name, setName] = useState('')
  const [command, setCommand] = useState('cl')
  const [delay, setDelay] = useState(1000)
  const [isActive, setIsActive] = useState(false)
  const [isLoading, setIsLoading] = useState(false)
  const [error, setError] = useState('')
  const [success, setSuccess] = useState('')
  const [hasToken, setHasToken] = useState(false)
  const [logs, setLogs] = useState<string[]>([])
  
  const wsRef = useRef<WebSocket | null>(null)
  const heartbeatRef = useRef<NodeJS.Timeout | null>(null)
  const activeCommandsRef = useRef<Set<string>>(new Set())

  useEffect(() => {
    const token = localStorage.getItem('discordToken')
    setHasToken(!!token)
    if (token) {
      loadCommands()
      loadGuilds()
    }
    
    return () => {
      if (wsRef.current) {
        wsRef.current.close()
      }
      if (heartbeatRef.current) {
        clearInterval(heartbeatRef.current)
      }
    }
  }, [])

  const loadCommands = () => {
    const saved = localStorage.getItem('autoCommands')
    if (saved) {
      setCommands(JSON.parse(saved))
    }
  }

  const loadGuilds = async () => {
    try {
      const token = localStorage.getItem('discordToken')
      if (!token) return

      const response = await fetch('https://discord.com/api/v10/users/@me/guilds', {
        headers: {
          'Authorization': token,
          'Content-Type': 'application/json'
        }
      })

      if (response.ok) {
        const data = await response.json()
        setGuilds(data)
      }
    } catch (err) {
      console.error('Erro ao carregar servidores:', err)
    }
  }

  const saveCommands = (newCommands: AutoCommand[]) => {
    localStorage.setItem('autoCommands', JSON.stringify(newCommands))
    setCommands(newCommands)
  }

  const addLog = (message: string) => {
    const timestamp = new Date().toLocaleTimeString('pt-BR')
    setLogs(prev => [`[${timestamp}] ${message}`, ...prev.slice(0, 99)])
  }

  const connectWebSocket = async () => {
    const token = localStorage.getItem('discordToken')
    if (!token) {
      throw new Error('Token não encontrado')
    }

    return new Promise<void>((resolve, reject) => {
      try {
        // Obter gateway URL
        fetch('https://discord.com/api/v10/gateway', {
          headers: {
            'Authorization': token,
            'Content-Type': 'application/json'
          }
        })
        .then(response => response.json())
        .then(gatewayData => {
          const gatewayUrl = `${gatewayData.url}/?v=10&encoding=json`
          wsRef.current = new WebSocket(gatewayUrl)

          wsRef.current.onopen = () => {
            addLog('WebSocket conectado')
          }

          wsRef.current.onmessage = (event) => {
            const data = JSON.parse(event.data)
            
            switch (data.op) {
              case 10: // Hello
                // Iniciar heartbeat
                if (heartbeatRef.current) {
                  clearInterval(heartbeatRef.current)
                }
                
                heartbeatRef.current = setInterval(() => {
                  if (wsRef.current && wsRef.current.readyState === WebSocket.OPEN) {
                    wsRef.current.send(JSON.stringify({
                      op: 1,
                      d: null
                    }))
                  }
                }, data.d.heartbeat_interval)

                // Enviar identificação
                const identifyPayload = {
                  op: 2,
                  d: {
                    token: token,
                    intents: 512, // GUILD_MESSAGES intent
                    properties: {
                      os: 'browser',
                      browser: 'discord-tools',
                      device: 'discord-tools'
                    }
                  }
                }
                
                wsRef.current?.send(JSON.stringify(identifyPayload))
                break
                
              case 0: // Dispatch
                if (data.t === 'READY') {
                  addLog('Bot conectado e pronto para monitorar mensagens')
                  resolve()
                } else if (data.t === 'MESSAGE_CREATE') {
                  handleMessageCreate(data.d)
                }
                break
            }
          }

          wsRef.current.onerror = (error) => {
            addLog('Erro na conexão WebSocket')
            reject(new Error('Erro na conexão WebSocket'))
          }

          wsRef.current.onclose = () => {
            addLog('WebSocket desconectado')
            if (heartbeatRef.current) {
              clearInterval(heartbeatRef.current)
            }
          }
        })
        .catch(err => {
          reject(new Error(`Erro ao obter gateway: ${err.message}`))
        })
      } catch (err: any) {
        reject(new Error(`Erro ao conectar WebSocket: ${err.message}`))
      }
    })
  }

  const handleMessageCreate = async (messageData: any) => {
    // Verificar se é uma mensagem do próprio usuário
    const token = localStorage.getItem('discordToken')
    if (!token) return

    try {
      const userResponse = await fetch('https://discord.com/api/v10/users/@me', {
        headers: {
          'Authorization': token,
          'Content-Type': 'application/json'
        }
      })

      if (!userResponse.ok) return

      const userData = await userResponse.json()
      
      // Se a mensagem não é do usuário atual, ignorar
      if (messageData.author.id !== userData.id) return

      // Se a mensagem já é um comando, ignorar para evitar loop
      if (messageData.content.startsWith('/') || messageData.content.startsWith('!')) return

      // Executar comandos ativos
      const activeCommands = commands.filter(cmd => cmd.isActive)
      
      for (const cmd of activeCommands) {
        if (activeCommandsRef.current.has(cmd.id)) continue // Evitar execução múltipla
        
        activeCommandsRef.current.add(cmd.id)
        
        setTimeout(async () => {
          try {
            await sendCommand(messageData.channel_id, cmd.command)
            
            // Atualizar estatísticas
            const updatedCommands = commands.map(c => 
              c.id === cmd.id 
                ? { ...c, lastUsed: new Date().toISOString(), useCount: c.useCount + 1 }
                : c
            )
            saveCommands(updatedCommands)
            
            addLog(`Comando "${cmd.command}" executado no canal ${messageData.channel_id}`)
          } catch (err: any) {
            addLog(`Erro ao executar comando "${cmd.command}": ${err.message}`)
          } finally {
            activeCommandsRef.current.delete(cmd.id)
          }
        }, cmd.delay)
      }
    } catch (err) {
      console.error('Erro ao processar mensagem:', err)
    }
  }

  const sendCommand = async (channelId: string, commandText: string) => {
    const token = localStorage.getItem('discordToken')
    if (!token) {
      throw new Error('Token não encontrado')
    }

    const response = await fetch(`https://discord.com/api/v10/channels/${channelId}/messages`, {
      method: 'POST',
      headers: {
        'Authorization': token,
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({
        content: commandText
      })
    })

    if (!response.ok) {
      throw new Error(`Erro ${response.status}: ${response.statusText}`)
    }
  }

  const handleCreateCommand = () => {
    setError('')
    setSuccess('')

    if (!name.trim()) {
      setError('Por favor, digite um nome para o comando.')
      return
    }

    if (!command.trim()) {
      setError('Por favor, digite o comando.')
      return
    }

    const newCommand: AutoCommand = {
      id: Date.now().toString(),
      name: name.trim(),
      command: command.trim(),
      delay: delay,
      isActive: false,
      createdAt: new Date().toISOString(),
      useCount: 0
    }

    const updatedCommands = [...commands, newCommand]
    saveCommands(updatedCommands)

    setSuccess('Comando criado com sucesso!')
    
    // Limpar formulário
    setName('')
    setCommand('cl')
    setDelay(1000)
  }

  const handleToggleCommand = async (commandId: string) => {
    const cmd = commands.find(c => c.id === commandId)
    if (!cmd) return

    setIsLoading(true)
    setError('')
    setSuccess('')

    try {
      if (!cmd.isActive) {
        // Ativar comando - conectar WebSocket se necessário
        if (!wsRef.current || wsRef.current.readyState !== WebSocket.OPEN) {
          await connectWebSocket()
        }
      }

      const updatedCommands = commands.map(c => 
        c.id === commandId 
          ? { ...c, isActive: !c.isActive }
          : c
      )
      saveCommands(updatedCommands)

      const activeCount = updatedCommands.filter(c => c.isActive).length
      
      if (activeCount === 0 && wsRef.current) {
        wsRef.current.close()
        addLog('Todos os comandos desativados - WebSocket desconectado')
      }

      setSuccess(`Comando ${cmd.isActive ? 'desativado' : 'ativado'} com sucesso!`)
    } catch (err: any) {
      setError(`Erro ao ${cmd.isActive ? 'desativar' : 'ativar'} comando: ${err.message}`)
    } finally {
      setIsLoading(false)
    }
  }

  const handleDeleteCommand = (commandId: string) => {
    const updatedCommands = commands.filter(c => c.id !== commandId)
    saveCommands(updatedCommands)
    setSuccess('Comando removido com sucesso!')
  }

  const formatDateTime = (dateString: string) => {
    return new Date(dateString).toLocaleString('pt-BR')
  }

  if (!hasToken) {
    return (
      <div className="max-w-2xl mx-auto text-center space-y-8 animate-fade-in">
        <div className="space-y-4">
          <div className="w-20 h-20 bg-gradient-to-r from-red-500 to-red-600 rounded-full flex items-center justify-center mx-auto">
            <span className="text-3xl">🔒</span>
          </div>
          <h1 className="text-3xl font-bold text-white">Token Não Configurado</h1>
          <p className="text-gray-300 text-lg">
            Configure seu token Discord para usar comandos automáticos.
          </p>
        </div>
        <div className="flex flex-col sm:flex-row gap-4 justify-center">
          <a 
            href="/config" 
            className="px-8 py-4 bg-gradient-to-r from-purple-600 to-purple-700 hover:from-purple-500 hover:to-purple-600 rounded-lg font-semibold transition-all duration-300 transform hover:scale-105"
          >
            Configurar Token
          </a>
          <a 
            href="/tutorial" 
            className="px-8 py-4 border border-purple-600 hover:bg-purple-600/10 rounded-lg font-semibold transition-all duration-300 transform hover:scale-105"
          >
            Ver Tutorial
          </a>
        </div>
      </div>
    )
  }

  return (
    <div className="max-w-6xl mx-auto space-y-8 animate-fade-in">
      <div className="text-center space-y-4">
        <h1 className="text-4xl font-bold bg-gradient-to-r from-cyan-400 to-cyan-600 bg-clip-text text-transparent">
          Comandos Automáticos
        </h1>
        <p className="text-gray-300 text-lg">
          Execute comandos automaticamente sempre que você enviar uma mensagem em qualquer servidor.
        </p>
      </div>

      {/* Status da Conexão */}
      <div className={`bg-gray-900/50 border rounded-xl p-4 transition-all duration-300 ${
        wsRef.current?.readyState === WebSocket.OPEN
          ? 'border-green-600/50 bg-green-900/10' 
          : 'border-gray-800/30'
      }`}>
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-4">
            <div className={`w-4 h-4 rounded-full transition-all duration-300 ${
              wsRef.current?.readyState === WebSocket.OPEN
                ? 'bg-green-500 animate-pulse' 
                : 'bg-gray-500'
            }`}></div>
            <div>
              <h3 className="text-lg font-semibold text-white">
                {wsRef.current?.readyState === WebSocket.OPEN ? 'Conectado' : 'Desconectado'}
              </h3>
              <p className="text-gray-400 text-sm">
                {wsRef.current?.readyState === WebSocket.OPEN 
                  ? 'Monitorando mensagens em tempo real'
                  : 'Ative um comando para conectar automaticamente'
                }
              </p>
            </div>
          </div>
          
          <div className="text-right">
            <div className="text-sm text-gray-400">Comandos Ativos</div>
            <div className="text-2xl font-bold text-cyan-400">
              {commands.filter(c => c.isActive).length}
            </div>
          </div>
        </div>
      </div>

      <div className="grid lg:grid-cols-2 gap-8">
        {/* Formulário de Criação */}
        <div className="bg-gray-900/50 border border-cyan-800/30 rounded-xl p-8 space-y-6">
          <h2 className="text-2xl font-bold text-white">Criar Comando Automático</h2>
          
          <div className="space-y-4">
            <div className="space-y-2">
              <label htmlFor="name" className="block text-sm font-medium text-cyan-300">
                Nome do Comando *
              </label>
              <input
                id="name"
                type="text"
                placeholder="Ex: Auto CL"
                value={name}
                onChange={(e) => setName(e.target.value)}
                className="w-full p-4 bg-gray-800/50 border border-gray-700 rounded-lg focus:border-cyan-500 focus:ring-2 focus:ring-cyan-500/20 transition-all duration-300 text-white placeholder-gray-400"
              />
            </div>

            <div className="space-y-2">
              <label htmlFor="command" className="block text-sm font-medium text-cyan-300">
                Comando *
              </label>
              <input
                id="command"
                type="text"
                placeholder="cl"
                value={command}
                onChange={(e) => setCommand(e.target.value)}
                className="w-full p-4 bg-gray-800/50 border border-gray-700 rounded-lg focus:border-cyan-500 focus:ring-2 focus:ring-cyan-500/20 transition-all duration-300 text-white placeholder-gray-400"
              />
              <p className="text-xs text-gray-400">Comando que será executado (sem / ou !)</p>
            </div>

            <div className="space-y-2">
              <label htmlFor="delay" className="block text-sm font-medium text-cyan-300">
                Delay (milissegundos)
              </label>
              <input
                id="delay"
                type="number"
                placeholder="1000"
                value={delay}
                onChange={(e) => setDelay(parseInt(e.target.value) || 1000)}
                min="100"
                max="10000"
                className="w-full p-4 bg-gray-800/50 border border-gray-700 rounded-lg focus:border-cyan-500 focus:ring-2 focus:ring-cyan-500/20 transition-all duration-300 text-white placeholder-gray-400"
              />
              <p className="text-xs text-gray-400">Tempo de espera antes de executar o comando</p>
            </div>

            <button
              onClick={handleCreateCommand}
              disabled={isLoading || !name.trim() || !command.trim()}
              className="w-full px-6 py-4 bg-gradient-to-r from-cyan-600 to-cyan-700 hover:from-cyan-500 hover:to-cyan-600 disabled:from-gray-600 disabled:to-gray-700 rounded-lg font-semibold transition-all duration-300 transform hover:scale-105 disabled:scale-100 disabled:cursor-not-allowed"
            >
              <span className="mr-2">⚡</span>
              Criar Comando
            </button>
          </div>
        </div>

        {/* Lista de Comandos */}
        <div className="bg-gray-900/50 border border-cyan-800/30 rounded-xl p-8 space-y-6">
          <div className="flex items-center justify-between">
            <h2 className="text-2xl font-bold text-white">Comandos Salvos</h2>
            <span className="px-3 py-1 bg-cyan-600/20 text-cyan-300 text-sm rounded-full">
              {commands.length}
            </span>
          </div>

          <div className="space-y-4 max-h-96 overflow-y-auto">
            {commands.length === 0 ? (
              <div className="text-center py-8 space-y-4">
                <div className="w-16 h-16 bg-gray-800 rounded-full flex items-center justify-center mx-auto">
                  <span className="text-2xl">⚡</span>
                </div>
                <p className="text-gray-400">Nenhum comando automático criado</p>
              </div>
            ) : (
              commands.map((cmd, index) => (
                <div
                  key={cmd.id}
                  className={`bg-gray-800/50 border rounded-lg p-4 space-y-3 animate-slide-in-left ${
                    cmd.isActive ? 'border-green-600/50 bg-green-900/10' : 'border-gray-700'
                  }`}
                  style={{ animationDelay: `${index * 0.1}s` }}
                >
                  <div className="flex items-center justify-between">
                    <div className="flex items-center space-x-3">
                      <span className="font-medium text-white">{cmd.name}</span>
                      {cmd.isActive && (
                        <span className="px-2 py-1 bg-green-600/20 text-green-300 text-xs rounded-full">
                          Ativo
                        </span>
                      )}
                    </div>
                    <div className="flex items-center space-x-2">
                      <button
                        onClick={() => handleToggleCommand(cmd.id)}
                        disabled={isLoading}
                        className={`px-3 py-1 text-white text-sm rounded-lg transition-all duration-300 transform hover:scale-105 disabled:scale-100 ${
                          cmd.isActive 
                            ? 'bg-red-600 hover:bg-red-500 disabled:bg-gray-600'
                            : 'bg-green-600 hover:bg-green-500 disabled:bg-gray-600'
                        }`}
                      >
                        {cmd.isActive ? 'Desativar' : 'Ativar'}
                      </button>
                      <button
                        onClick={() => handleDeleteCommand(cmd.id)}
                        className="text-red-400 hover:text-red-300 transition-colors"
                      >
                        🗑️
                      </button>
                    </div>
                  </div>
                  
                  <div className="space-y-1 text-sm text-gray-400">
                    <p>Comando: <span className="text-cyan-300">{cmd.command}</span></p>
                    <p>Delay: {cmd.delay}ms</p>
                    <p>Usado: {cmd.useCount} vezes</p>
                  </div>
                  
                  <div className="flex items-center justify-between text-xs text-gray-500">
                    <span>Criado: {formatDateTime(cmd.createdAt)}</span>
                    {cmd.lastUsed && (
                      <span>Último uso: {formatDateTime(cmd.lastUsed)}</span>
                    )}
                  </div>
                </div>
              ))
            )}
          </div>
        </div>
      </div>

      {/* Logs */}
      <div className="bg-gray-900/50 border border-cyan-800/30 rounded-xl p-6 space-y-4">
        <div className="flex items-center justify-between">
          <h2 className="text-xl font-bold text-white">Logs de Atividade</h2>
          <button
            onClick={() => setLogs([])}
            className="px-3 py-1 bg-gray-600 hover:bg-gray-500 text-white text-sm rounded-lg transition-all duration-300"
          >
            Limpar
          </button>
        </div>
        
        <div className="bg-gray-800/50 rounded-lg p-4 max-h-64 overflow-y-auto font-mono text-sm">
          {logs.length === 0 ? (
            <p className="text-gray-400">Nenhuma atividade registrada</p>
          ) : (
            logs.map((log, index) => (
              <div key={index} className="text-gray-300 py-1">
                {log}
              </div>
            ))
          )}
        </div>
      </div>

      {/* Mensagens de Status */}
      {error && (
        <div className="bg-red-900/30 border border-red-600/30 rounded-lg p-4 text-red-300 animate-slide-in-left">
          {error}
        </div>
      )}

      {success && (
        <div className="bg-green-900/30 border border-green-600/30 rounded-lg p-4 text-green-300 animate-slide-in-left">
          {success}
        </div>
      )}

      {/* Aviso */}
      <div className="bg-yellow-900/20 border border-yellow-600/30 rounded-xl p-6 space-y-3">
        <div className="flex items-center space-x-2">
          <span className="text-2xl">⚠️</span>
          <h3 className="text-lg font-semibold text-yellow-300">Importante</h3>
        </div>
        <div className="text-yellow-200 space-y-2 text-sm">
          <p>• Os comandos são executados automaticamente após você enviar qualquer mensagem</p>
          <p>• Comandos que começam com / ou ! são ignorados para evitar loops</p>
          <p>• Use delays apropriados para evitar spam e rate limiting</p>
          <p>• A ferramenta monitora mensagens em tempo real via WebSocket</p>
          <p>• Desative comandos quando não precisar para economizar recursos</p>
        </div>
      </div>
    </div>
  )
}
